<template>
    <div class="list-container" ref="list-container"> 
        <!-- <a :href="ApiConfig.domain + item.link" v-for="item in listData" :key="item.id" target="_blank" class="list-item"> -->
        <a :href="'/ui/#/show?chnid='+item.id" v-for="item in listData" :key="item.id" class="list-item">
            <img :src="ApiConfig.domain + item.img" alt="">
            <div class="item-title">{{item.name}}</div>
            <div class="item-description">{{item.descript}}</div>
        </a>
    </div>
</template>

<script>
export default {
    props: ['listData'],
}
</script>





