export default {
    state: {
        loading: false,
        alert: {
            text: null,
            time: 2000,
        },
    },
    mutations: {
        loading(state,status){
            state.loading = status
        },
        alert(state,data){
            state.alert.text = data.text;
            state.alert.time = data.time ? data.time : 2000;
        }

    }
}


