<template>
    <div class="show-app app-container">
		<video-component></video-component>
        <notice-component :words="notice" v-show="!!notice"></notice-component>
        <component :is="currentComponent"></component>
	</div>
</template>
<script>

import VideoComponent from '@/components/VideoComponent.vue'
import NoticeComponent from '@/components/NoticeComponent.vue'
import WaitComponent from '@/components/WaitComponent.vue'
import TabComponent from '@/components/TabComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: 'ShowPage',
    data(){
        return {
            notice: null,           //  通知公告内容
            zoneNotifyTemp: [],     //  蛇形公告临时队列
            currentComponent: null, //  当前活跃组件
            activeinterval: {
                original: null,     //  最大活跃间隔
                now: 0,             //  计算器
            },
            intervals: {
                heartInterval: null,   //   心跳定时器
                noticeInterval: null,  //   公告定时器
                activeInterval: null,  //   活跃定时器
                zoneNoticeInterval: null,   //  侧栏定时器
            }
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
            onlineIDs: state => state.HeartBeanService.onlineIds
        }),
        ...mapGetters(['firstNotice','onlinIDArr','zoneNotifys']),
    },
    watch:{
        baseInfo: function(data){
            if(data.airtime && data.airtime > parseInt(new Date().getTime()/1000)){
                this.currentComponent = 'wait-component'
            }else{
                this.currentComponent = 'tab-component'
            }
        },
    },
    methods:{
        //  触发页面关闭，结束所有在线计费
        pageDestory(){
            //  清除在线记录
            this.onlinIDArr.forEach((id) => {
                this.$store.commit('removeOnlineId',id);
                this.$store.dispatch('stopOnlineJson',{
                    onlineId: id
                })
            })
            //  清除定时器
            clearInterval(this.intervals.heartInterval);
            clearInterval(this.intervals.noticeInterval); 
            clearInterval(this.intervals.activeInterval); 
            //  清除vuex数据
            this.$store.commit('clearService');
            //  清除baseInfo数据
            this.$store.commit('clearBaseInfo')
        }
    },
    beforeDestroy(){
        //  页面卸载，关闭在线
        this.pageDestory();
    },
    components:{
        VideoComponent,
        NoticeComponent,
        WaitComponent,
        TabComponent    
    },
    created: function(){
        //  监听页面退出
        window.onbeforeunload = () => this.pageDestory();  
        window.onunload = () => this.pageDestory();

        this.$nextTick(()=>{
            //  判断身份
            this.$store.dispatch('getUserInitialJson').then(({data}) => {
                if(data.userinfo){
                    
                }else{
                    // this.$router.push({path:'/login'})
                }
            });

            this.$store.commit('setChnid',this.$route.query.chnid);
            //  获取基础信息构造参数
            const params = {chnid: this.$route.query.chnid}
            //  获取可选额外参数
            if(this.$route.query.vod){ params.vod = this.$route.query.vod;}
            if(this.$route.query.rcm){ params.rcm = this.$route.query.rcm;}

            //  获取基础信息
            this.$store.dispatch('getRoomInitialJson',params).then(({data}) => {
                if(data.noright){
                    //  无权访问
                    alert(data.noright.msg);
                    window.location.href = data.noright.link;
                    return false;
                }else{
                    //  1、保存基础信息
                    this.$store.commit('setBaseInfo',data);
                    //  2、设置页面信息
                    document.title = data.title;
                    //  3、提交web onlineId
                    this.$store.commit('pushOnlineId',{
                        name: 'web',
                        value : data.onlineid
                    });
                    //  4、设置最大活动间隔
                    if(this.activeinterval.original != 0){
                        //  设置最大时间
                        this.activeinterval.original = data.activeinterval;
                        let notify_time = 30;   //  通知时间 30s
                        this.intervals.activeInterval = setInterval(() => {
                            //  当计算器为0时，
                            this.activeinterval.now++;
                            let time = this.activeinterval.original - this.activeinterval.now;
                            if(time <= notify_time){
                                this.$store.commit('alert',{
                                    text: `您在当前页面非活动时间过长，页面将于${time}秒后退出`,
                                    time: notify_time*1000,
                                })
                            }
                            if(this.activeinterval.now >= this.activeinterval.original){
                                //  超时
                                window.location.href = '/ui'
                            }
                        },1000);
                        //  触摸屏幕进行重新进入活跃状态
                        window.addEventListener('touchstart',() => {
                            this.activeinterval.now = 0;
                            this.$store.commit('alert',{});
                        })
                    }

                    //  5、建立心跳通讯
                    const startHeart = () =>{
                        this.$store.dispatch('getHeartbeatJson',{
                            onlineID: this.onlinIDArr
                        }).then(({data}) => {

                            //  ===================================
                            //  弹幕发送设置
                            //  ===================================
                            const chat =  [];   
                            let i = 0;
                            const startChat = () =>{
                                if(chat[i]){
                                    this.$store.commit('addChat',chat[i]);
                                    i++;
                                }else{
                                    clearInterval(chat_timer)
                                }
                            }
                            //  弹幕发送频率定时器：间隔 = 心跳间隔/当前心跳弹幕数目
                            var chat_timer = setInterval(() => {
                                startChat()
                            },Math.floor(interval_timer * 1000 / chat.length));
                            startChat()


                            //  ===================================
                            //  礼物发送设置
                            //  ===================================
                            const gifts =  [];  //  礼物队列
                            let k = 0;
                            const startGift = () =>{
                                if(gifts[k]){
                                    this.$store.commit('addGifts',gifts[k]);
                                    k++;
                                }else{
                                    clearInterval(gift_timer)
                                }
                            }
                            //  弹幕发送频率定时器：间隔 = 心跳间隔/当前心跳弹幕数目
                            var gift_timer = setInterval(() => {
                                startGift()
                            },Math.floor(interval_timer * 1000 / gifts.length));
                            startGift()

                            
                            //  ===================================
                            //  公告发送设置
                            //  ===================================

                            var zone_broacast = [];     //  蛇形
                            var normal_broacast = [];   //  走马灯
                            data.broadcast.forEach((broadcast) => {
                                if(broadcast.zone == 1){
                                    zone_broacast.push(broadcast)
                                }else if(broadcast.zone == 2){
                                    normal_broacast.push(broadcast)
                                }
                            })
                            //  添加到蛇形队列
                            if(zone_broacast){
                                zone_broacast.forEach((item) => {
                                    this.zoneNotifyTemp.push(item);  //  添加到暂存队列里
                                })
                            }
                            //  添加到走马灯
                            if(normal_broacast){this.$store.commit('addNotify',normal_broacast);}

                            //  ===================================
                            //  抽奖设置
                            //  ===================================
                            if(data.boxs){
                                // data.boxs.forEach((data)=>{ 
                                //     data.enroll_b = Math.floor(new Date().getTime()/1000  + 10)
                                //     data.enroll_e = Math.floor(new Date().getTime()/1000  + 20)
                                //     data.draw = Math.floor(new Date().getTime()/1000  + 22)
                                // })
                                this.$store.commit('addBoxes',data.boxs);
                            }
                        })  
                    }
                    //  6、设置心跳通讯定时器
                    let interval_timer = this.baseInfo.hbinterval || 10;
                    this.intervals.heartInterval = setInterval(() => {
                        startHeart();
                    }, interval_timer * 1000);
                    startHeart(); 
                }
            })
            
            //  监听公告定时器
            this.intervals.noticeInterval = setInterval(() => {
                if(this.firstNotice && this.firstNotice.stamp <= new Date().getTime()/1000){
                    this.notice = this.firstNotice.content;
                    this.$store.commit('removeNotify');
                }else{
                    this.notice = null;
                }
            },this.ApiConfig.notice_interval);  
            
            //  监听蛇型公告定时器
            this.intervals.zoneNoticeInterval = setInterval(() => {
                if(this.zoneNotifys.length < 4 && this.zoneNotifyTemp.length > 0){
                    //  从暂存队列移交到显示队列上
                    this.$store.commit('addZoneNotify',this.zoneNotifyTemp.shift());
                    //  设置定时器 删除侧栏
                    let timer = setTimeout(() => {
                        this.$store.commit('removeZoneNotify');
                        clearTimeout(timer);
                    },this.baseInfo.hbinterval * 1000 + Math.random() * 1000);  //  随机数制造时间差
                }               
            },this.ApiConfig.zone_notice_interval)

        })
    }
}
</script>



