<template>
    <div class="c-wait" id="c-wait">
        <div class="wait-title">现场直播：《{{baseInfo.title}}》</div>
        <div class="wait-plane">
            <span class="wait-plane-title">本次直播将于 {{airTimeFormat}} 开始</span>
            <p class="wait-text">直播倒计时</p>
            <!-- 根据data-time的开播时间戳来倒计时 -->
            <div class="wait-count" >
                <span>{{dayHtml}}</span> 天
                <span>{{hourHtml}}</span> 时
                <span>{{minuteHtml}}</span> 分
                <span>{{secondHtml}}</span> 秒
            </div>
        </div>
        <a href="" class="wait-subscribe">点击此处订阅开播提醒》</a>
        <div class="wait-rich-text">
            <rich-text-component :data="baseInfo.intr" v-if="baseInfo.intr"></rich-text-component>
        </div>
    </div>
</template>
<script>

import RichTextComponent from '@/components/RichTextComponent.vue'
import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    data(){
        return {
            dayHtml: '00',
            hourHtml: '00',
            minuteHtml: '00',
            secondHtml: '00',
            interval_timer: null,
        }
    },
    computed:{
        airTimeFormat: function(){
            return this.formatDateTime(this.baseInfo.airtime)
        },
        ...mapState({
            baseInfo: state => state.BaseInfoService.baseInfo,
        })
    },
    methods:{
        //  时间戳转格式
        formatDateTime(inputTime) {
            var date = new Date(inputTime);
            var y = date.getFullYear();
            var m = date.getMonth() + 1;
            m = m < 10 ? ('0' + m) : m;
            var d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            var h = date.getHours();
            h = h < 10 ? ('0' + h) : h;
            var minute = date.getMinutes();
            var second = date.getSeconds();
            minute = minute < 10 ? ('0' + minute) : minute;
            second = second < 10 ? ('0' + second) : second;
            return m + '-' + d+' '+h+' : '+minute;
        },
        wait_count(){
            var dist_time = this.baseInfo.airtime;
            var now_time = parseInt(new Date().getTime()/1000);
            var diff_time = dist_time - now_time;
            var day = Math.floor(diff_time/(86400));
            if(day < 10){
                day =('00' + day.toString()).slice(-2);
            }
            this.dayHtml = day
            this.hourHtml =  ('00' + Math.floor(diff_time%(86400) / (3600)).toString()).slice(-2)
            this.minuteHtml = ('00' + Math.floor(diff_time%(3600) / (60)).toString()).slice(-2)
            this.secondHtml = ('00' + Math.floor(diff_time%(60)).toString()).slice(-2)
            return diff_time;
        }
    },
    components:{
        RichTextComponent
    },
    mounted:function(){
        this.$nextTick(()=>{
            var interval_timer = setInterval(() => {
                if(!this.wait_count()){
                    clearInterval(interval_timer);
                    window.location.reload()
                }
            },1000)
        })
    },
    beforeDestroy(){
        clearInterval(interval_timer);
    }
}
</script>



