<template>
    <div class="chat-window">
        <div class="chat-code" v-if="data.type == 1 || data.type == 2">
            <p v-if="data.msg">{{data.msg}}</p>
            <input type="text" class="chat-input" v-if="data.type == 2" v-model="keyword" placeholder="请输入抽奖口令">
            <div class="chat-confirm"><button @click="confirm">确认</button></div>
            <span class="chat-window-close" @click="close">×</span>
        </div>
        <div class="chat-guess" v-else-if="data.type == 3">
            <p v-if="data.msg">{{data.msg}}</p>
            <div class="chat-guess-options">
                <a href="javascript:;" 
                    :class="[selects.indexOf(index) >= 0 ? 'active' : '']" 
                    @click="selects.indexOf(index) >= 0 ? selects.splice(selects.indexOf(index),1) : selects.push(index)" 
                    v-for="(item,index) in data.selects">{{item.text}}</a>
            </div>
            <div class="chat-confirm"><button @click="confirm">确认</button></div>
            <span class="chat-window-close" @click="close">×</span>
         </div>
    </div>
</template>

<script>
export default {
    props:['data'],
    data(){
        return {
            keyword: null,
            selects: []
        }
    },
    methods:{
        close(){
            this.$emit('close')
        },
        confirm(){
            var result_data = {};
            if(this.data.type == 1){
                this.$emit('close');
                return ;
            }else if(this.data.type == 2){
                result_data = this.keyword;
            }else if(this.data.type == 3){
                var arr = [];
                this.data.selects.forEach((element,index) => {
                    if(this.selects.indexOf(index) >= 0){
                        arr.push(element.text);
                    }
                });
                result_data = arr;
            }
            this.$emit('confirm',{
                lotteryid: this.data.lotteryid,
                type: this.data.type,
                data: result_data
            })
        }
    }
}
</script>




