<template>
    <div class="scroll-notice" id="v-notice">
    </div>
</template>
<script>
export default {
    name: 'NoticeComponent',
    props:['words'],
    watch:{
        words: function(data){
            var html = `<span class="scroll">${this.words}</span>`;
            $('#v-notice').html(html);
            var notice_width = $('#v-notice').find('span').innerWidth() + parseInt($('#v-notice').width());
            $('#v-notice').find('.scroll').css({'transform':'translateX(-'+ ( notice_width )+'px)'})
        }
    }
}
</script>




