<template>
    <div class="tab-chat">
        <!-- 弹幕内容 Start -->
        <chat-msg-component :chat="chat" :gifts="gifts"></chat-msg-component>
        <!-- 弹幕内容 End -->

        <zone-notice-component></zone-notice-component>

        <!-- 宝箱 Start -->
        <box-component @lotteryShowJson="lotteryShowJson"></box-component>
        <!-- 宝箱 End -->

        <!-- 输入框 Start -->
        <div class="chat-bar" id="chat-bar">
            <router-link to="/" :class="'chat-back bg-tab_back'"></router-link>
            <input type="text" class="chat-input" id="chat-input" ref="chat-input" 
                v-on:click="clickBar()" 
                v-on:blur="blurBar()" 
                v-model="bar.message"
                placeholder="吐个槽呗~">
            <div class="chat-send" id="chat-send">
                <a href="" class="chat-more bg-tab_more" v-show="!bar.focus"></a>
                <div class="chat-submit" v-show="bar.focus" v-on:click="sendText()">发送</div>
            </div>
            <a href="javascript:;" class="bg-tab_gifts" v-on:click="gift.active = true"></a>
        </div>
        <!-- 输入框 Start -->

        <gift-component v-show="gift.active" :data="gift.data" v-if="gift.data.length" v-on:close-gift-window="gift.active = false"></gift-component>

        <confirm-component :data="confirmObj" v-if="confirmObj.type" @close="confirmObj = {}" @confirm="confirm"></confirm-component>

    </div>
</template>
<script>
import GiftComponent from '@/components/GiftComponent.vue'
import ChatMsgComponent from '@/components/ChatMsgComponent.vue'
import BoxComponent from '@/components/BoxComponent.vue'
import ConfirmComponent from '@/components/ConfirmComponent'
import ZoneNoticeComponent from '@/components/ZoneNoticeComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    data(){
        return {
            gift:{
                active: false,
                data: {}
            },
            bar:{
                focus: false,
                message: '',
            },
            confirmObj: {}
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,

            chat: state => state.HeartBeanService.chat,
            gifts: state => state.HeartBeanService.gifts,
        })
    },
    methods:{
        closeGiftWindow(){
            this.gift.active = false;
        },
        clickBar(){
            this.bar.focus = true;
            
            //  兼容安卓键盘弹出问题
            if(this.isAndroid){
                $('.chat-bar').css({
                    'position':'fixed',
                    'bottom':'0',
                    'z-index': '2'
                })
                $('.chat-msg').css({
                    'padding-bottom':$('.chat-bar').height() + 10,
                });
                $('.show-app').append($('#chat-bar'))
                $(this.$refs['chat-input']).focus();
            }
        },
        blurBar(){
            if(!this.$refs['chat-input'].value.trim()){
                this.bar.focus = false;
            }
            //  兼容安卓键盘弹出问题
            if(this.isAndroid){
                $('.tab-chat').append($('#chat-bar'));
            }
        },
        lotteryShowJson(data){
            if(data.success == 'true'){
                this.confirmObj = data;
            }else{
                this.$store.commit('alert',{
                    text: data.msg
                })
            }
        },
        confirm(params){
            this.$store.dispatch('enrollJson',params).then(({data}) => {
                this.confirmObj = {}
                this.$store.commit('alert',{
                    text: data.msg
                })
            })
        },
        sendText(){
            if(this.bar.message){
                this.$store.dispatch('newMsgJson',{
                    chnid: this.chnid,
                    message: this.bar.message
                }).then(({data}) =>{
                    if(data.success == 'true'){
                        data.chat.forEach((item) => {
                            this.$store.commit('addChat',{
                                sender: item.sendername,
                                userlever: item.userlevel,
                                viplevel: item.viplevel,
                                content: item.message,
                                stamp: Math.floor(new Date(item.sendtime).getTime()/1000),
                            })
                        })
                    }else{
                        this.$store.commit('alert',{
                            text: data.msg
                        })
                    }
                })
                this.bar.message = '';
            }else{
                this.$store.commit('alert',{
                    text: '内容不能为空'
                })
            }
        }
    },
    components:{
        GiftComponent,
        ChatMsgComponent,
        BoxComponent,
        ConfirmComponent,
        ZoneNoticeComponent
    },
    mounted: function(){
        this.$nextTick(function () {
            this.$store.dispatch('getGiftListJson',{
                chnid: this.chnid
            }).then(({data})=>{
                this.gift.data = data;
            })
        })
    }
}
</script>




