<template>
    <!-- 礼物框 Start -->
    <div class="gifts-window" v-on:click="closeGiftWindow">
        <div class="chat-gifts" onclick="event.stopPropagation()">
            <div class="gifts-container">
                <div class="swiper-wrapper" >
                    <div class="swiper-slide gifts-rows"  v-for="(item,index) in gifts">
                        <a href="javascript:;" :class="['gifts-item',currentGift.id == item.id ? 'active' :'']"  v-on:click="select_gift(item)" v-for="(item,index) in item"> 
                            <img :src="ApiConfig.domain+item.picture" :class="['gift-img',item.disableNote ? 'ban' :'']" :alt="item.name">
                            <div class="gifts-value"><i class="gifts-icon"></i><p>{{item.price}}</p></div>
                            <span>{{item.name}}</span>
                        </a>
                    </div>
                </div>
                <div class="gifts-pagination"></div>
            </div>
            <div class="gifts-handle">
                <div class="gifts-text" ref="gifts-text">
                    <p v-show="currentGift.id">送出 <span>{{currentGift.name}}</span> 总价：<span class="acColor">{{currentGift.price * currentNum}}</span></p>
                </div>
                <div class="gifts-num">
                    <a href="javascript:;" v-on:click="currentNum--" >-</a>
                    <span>{{currentNum}}</span>
                    <a href="javascript:;" v-on:click="currentNum++" >+</a>
                </div>
                <button class="gifts-send" id="gifts-send">发送</button>
            </div>
        </div>
    </div>
    <!-- 礼物框 End -->
</template>

<script>
import Swiper from 'swiper';

export default {
    props:['data'],
    data(){
        return {
            currentGift: {},
            currentNum: 1,
        }
    },
    computed:{
        gifts:function(){
            const slide = [];
            let i = -1;
            this.data.sort((a,b) => {
                return b.order - a.order;
            }).forEach((value,index) => {
                if(index%8 == 0){
                    i++;
                    slide[i] = [];
                }
                slide[i].push(value);
            });
            return slide;
        }
    },
    methods: {
        initSwiper() {
            new Swiper('.gifts-container', {
                pagination: {
                    el: '.gifts-pagination',
                    bulletClass : 'gifts-bullet',
                    bulletActiveClass: 'gifts-bullet-active',
                },
                observer:true,      //修改swiper自己或子元素时，自动初始化swiper
                observeParents:true,//修改swiper的父元素时，自动初始化swiper
            });
        },
        initGift(){
            this.currentGift = {};
            this.currentNum = 1;
        },
        select_gift(item) {
            if(item.disableNote){
                this.$store.commit('alert',{
                    text: item.disableNote,
                    time: 3000
                });
            }else{
                this.currentGift = item;
            }
        },
        closeGiftWindow(){
            this.$emit('close-gift-window');
            this.initGift();

        }
    },
    mounted: function(){
        this.$nextTick(function(){
            this.initSwiper();
        })
    },
}
</script>



