import Vue from 'vue'
import API from '../config/api'
function apiFactory(api,type='GET'){
    return function(params = null){
        if(type === 'GET'){
            return Vue.http({
                url:api.url,
                method: type,
                emulateJSON: true,
                params: api.params ? api.params(params) : null,
            })
        }else{
            return Vue.http({
                url:api.url,
                method: type,
                emulateJSON: true,
                body: api.params ? api.params(params) : null,
            })
        }
    }
}
export default {
    state: {
        room: {
            isEnd: false,           //  是否结束
            listLoading: false,     //  是否正在加载
            listData: [],           //  房间列表数据
            listConfig: {           //  房间配置信息
                page: 1,
                rows: 10,
                sort: '最热',
                categoryid: null,
                searchStr: ''
            }
        }
        
    },
    mutations:{
        initList(state,config){
            state.room.listData = [];
            state.room.listConfig.page = 1;
            state.room.listConfig.sort = config.sort || state.room.listConfig.sort;
        },
        updataListConfig(state,data){
            state.room.listConfig = data;
        },
        loadListData(state,data){
            state.room.listData = state.room.listData.concat(data);
        },
        loadingList(state,status){
            state.room.listLoading = status;
        },
        endList(state,status){
            state.room.isEnd = status;
        },
    },
    actions: {
        //  获取Banner
        getBanner() {
            return apiFactory(API.banner)();
        },
        //  获取第三方登陆信息
        getThirdUrl() {
            return apiFactory(API.thirdLink)();
        },
        //  获取频道分类
        getRoomType(){
            return apiFactory(API.roomType)();
        },
        //  频道播放页面基础信息
        getUserInitialJson({state},params){
            return apiFactory(API.userInitialJson)();
        },
        //  获取频道列表
        loadListData(context,params){
            var state = context.state;
            if(!state.room.isEnd){
                context.commit('loadingList',true);
                //  更新配置项
                context.commit('updataListConfig',{
                    page: state.room.listConfig.page,
                    rows: state.room.listConfig.rows,
                    sort: params.sort || state.room.listConfig.sort,
                    categoryid: params.categoryid != undefined ?  params.categoryid : null,
                    searchStr: params.searchStr != undefined ?  params.searchStr : null,
                })
                return apiFactory(API.listData,'POST')(state.room.listConfig).then(({data}) => {
                    if(data.length){
                        context.commit('loadListData',data)
                    }else{
                        context.commit('endList',true);
                    }
                    context.commit('loadingList',false);
                });
            }
        },
        //  频道播放页面基础信息
        getRoomInitialJson({state},params){
            return apiFactory(API.roomInitialJson)(params);
        },
        //  获取直播地址
        getLiveUriInfo({state},params){
            return apiFactory(API.liveUriInfo)(params);
        },
        //  获取视频地址
        getVodeUriInfo({state},params){
            return apiFactory(API.vodUriInfo)(params);
        },
        //  获取排行榜信息
        getRankingJson({state},params){
            return apiFactory(API.rankingJson)(params);
        },
        //  获取礼物接口
        getGiftListJson({state},params){
            return apiFactory(API.giftListJson)(params);
        },
        //  开始在线
        startOnlineJson({state},params){
            return apiFactory(API.startOnlineJson)(params);
        },
        //  结束在线
        stopOnlineJson({state},params){
            return apiFactory(API.stopOnlineJson)(params);
        },
        //  心跳通讯
        getHeartbeatJson({state},params){
            return apiFactory(API.heartbeatJson,'POST')(params);  
        },
        //  显示抽奖信息
        lotteryShowJson({state},params){
            return apiFactory(API.lotteryShowJson)(params);
        },
        //  登记抽奖
        enrollJson({state},params){
            return apiFactory(API.enrollJson,'POST')(params);
        },
        //  发送弹幕
        newMsgJson({state},params){
            return apiFactory(API.newMsgJson,'POST')(params);
        },
        //  登陆操作
        doLogin({state},params) {
            return apiFactory(API.doLogin,'POST')(params);
        },
    }
}



