<template>
    <div>
        <div class="index-app app-container" ref="scrollContainer" >
            <banner-component :swiper=swiper v-if="swiper"></banner-component>
            <router-link to="/home" :class="'user-center'"></router-link>
            <list-component></list-component>
        </div>
        <a href="javascript:;" class="toTop" v-on:click="toTop" v-show="toTopShow"></a>
    </div>
</template>

<script>
import Vue from "vue";
import BannerComponent from "@/components/BannerComponent.vue";
import ListComponent from "@/components/ListComponent.vue"

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: "IndexPage",
    data() {
        return {
            swiper: null,
            toTopShow: false,
        };
    },
    mounted: function() {
        this.$store.dispatch('getUserInitialJson').then(({data}) => {
            if(data.userinfo){
                
            }else{
				this.$router.push({path:'/login'})
            }
        });

        this.$store.dispatch('getBanner').then(({data}) => {
            this.swiper = data;
        });
      
        //--------------上拉加载更多---------------
        //获取滚动条当前的位置
        function getScrollTop(scrollObj) {
            var scrollTop = 0;
            if(scrollObj && scrollObj.scrollTop) {
                scrollTop = scrollObj.scrollTop;
            } else if(document.body) {
                scrollTop = document.body.scrollTop;
            }
            return scrollTop;
        }

        //获取当前可视范围的高度
        function getClientHeight(scrollObj) {
            var clientHeight = 0;
            if(document.body.clientHeight && scrollObj.clientHeight) {
                clientHeight = Math.min(document.body.clientHeight, scrollObj.clientHeight);
            } else {
                clientHeight = Math.max(document.body.clientHeight, scrollObj.clientHeight);
            }
            return clientHeight;
        }
        //获取文档完整的高度
        function getScrollHeight(scrollObj) {
            return Math.max(document.body.scrollHeight, scrollObj.scrollHeight);
        }
        //滚动事件触发
		var scroll_container = this.$refs.scrollContainer;
		var that = this;
        if(scroll_container){
            scroll_container.onscroll = function() {
                let scroll_top = getScrollTop(this) ;
                if(scroll_top > 300){
                    that.toTopShow = true
                }else{
                    that.toTopShow = false
                }
                if(scroll_top + getClientHeight(this) + 60 > getScrollHeight(this)) {
					if(!that.listLoading){
						that.loadListData({
							page: that.listConfig.page++,
							rows: that.listConfig.rows,
							sort: that.listConfig.sort,
							categoryid: that.listConfig.categoryid,
							searchStr: that.listConfig.searchStr
						})
					}
                }
            }
        }
        //-----------------结束--------------------
    },
    computed:{
        ...mapState ({
			listConfig: state => state.ApiService.room.listConfig,
			listLoading: state => state.ApiService.room.listLoading
        })
    },
    methods:{
        loadListData(config = {}){
            this.$store.dispatch('loadListData',config)
        },
        toTop(){
            $(this.$refs.scrollContainer).animate({scrollTop:0},500);
        }
    },
    components: {
        BannerComponent,
        ListComponent
    }
};
</script>



