
import Config from '../config/ApiConfig'
export default {
    banner:{
        url: Config.domain+'/home.php/Home/loginBanner',
    },
    thirdLink: {
        url: Config.domain+'/home.php/Home/loginIniJson',
    },
    roomType: {
        url: Config.domain+'/home.php/Channel/chnCategoryJson'
    },
    listData: {
        url: Config.domain+'/home.php/Home/channelListJson',
        params: (params) => {
            return {
                type: 0,
                searchStr: params.searchStr,
                sort: params.sort,
                categoryid:params.categoryid,
                scrType: 'h',
                page: params.page,
                rows: params.rows,
            }
        },
    },
    userInitialJson:{
        url: Config.domain+'/home.php/Home/initialJson',
    },
    roomInitialJson:{
        url: Config.domain+'/home.php/Player/initialJson',
        params: (params) => {
            return {
                chnid: params.chnid,
                vod: params.vod,
                rcm: params.rcm
            }
        }
    },
    liveUriInfo: {
        url: Config.domain+'/home.php/Player/getLiveUriJson',
        params: (params) => {
            return {
                chnid: params.chnid,
            }
        }
    },
    vodUriInfo: {
        url: Config.domain+'/home.php/Player/getVodUriJson',
        params: (params) => {
            return {
                vodid: params.vodid,
            }
        }
    },
    rankingJson:{
        url: Config.domain+'/home.php/User/rankingJson',
        params: (params) => {
            return {
                chnId: params.chnid,
            }
        }
    }, 
    giftListJson:{
        url: Config.domain+'/home.php/Channel/giftListJson',
        params: (params) => {
            return {
                chnId: params.chnid,
            }
        }
    },
    startOnlineJson:{
        url: Config.domain+'/home.php/Player/startOnlineJson',
        params: (params) => {
            return {
                objType: params.objType,    //  计费对象类型。web|live|vod
                objId: params.objId         //  计费对象ID。web: objId=0; live: objId=频道ID；vod: objId=点播文件ID
            }
        }
    },
    stopOnlineJson:{
        url: Config.domain+'/home.php/Player/stopOnlineJson',
        params: (params) => {
            return {
                onlineId: params.onlineId
            }
        }
    },
    heartbeatJson:{
        url: Config.domain+'/home.php/Player/heartbeatJson',
        params: (params) => {
            return {
                onlineID: params.onlineID
            }
        }
    },
    lotteryShowJson:{
        url: Config.domain+'/home.php/Lottery/showJson',
        params: (params) => {
            return  {
                lotteryid: params.lotteryid
            }
        }
    },
    enrollJson:{
        url: Config.domain+'/home.php/Lottery/enrollJson',
        params: (params) => {
            return {
                lotteryid: params.lotteryid,
                type: params.type,
                data: params.data
            }
        }
    },
    newMsgJson:{
        url: Config.domain+'/home.php/WebChat/newMsgJson',
        params: (params) => {
            return {
                chnid: params.chnid,
                message: params.message
            }
        }
    },
    doLogin: {
        url: Config.domain+'/home.php/Home/authenJson',
        params: (params) => {
            return {
                account: params.account,
                password: params.password,
            }
        },
    },
}



