<template>
    <div class="c-video">
        <div class="v-player">
            <video :src="video.src" class="v-play-source" ref="video"
                x5-video-player-type="h5"
                x5-video-player-fullscreen="true"
                webkit-inline="true"
                x-webkit-airplay="allow"
                playsinline="true"
                webkit-playsinline="true"
                preload="auto"
                autoplay="true"
                :x5-video-orientation="x5VideoOrientation"
                :poster="poster"
                @ended="vodEnd"
                >
                </video>
            <img :src="poster" v-if="!hasInit" class="v-poster" alt="">
        </div>
        <div ref="video_controls" v-bind:class="['v-controls',controls_show ? '' : 'opacity-hide']" v-on:click="show_controls()">
            <div v-bind:class="['v-play',playing ? 'bg_video_pause' : 'bg_video_play']" ref="video_play" v-on:click.stop="viedo_switch"></div>
            <div class="v-fullscreen bg_video_fullscreen" ref="video_fullscreen" v-on:click="video_fullscreen"></div>
        </div>
    </div>
</template>

<script>
import {mapMutations, mapState, mapGetters} from 'vuex'
export default {
    name: "VideoComponent",
    data(){
        return {
            video:{                 //  视频信息
                src: '',     //  资源路径
            },
            playing: false,
            hasInit: false,
            controls_show: true,
            fullScreen: false,
            x5VideoOrientation: 'portrait',
            dom:{
                videoPlayer: null
            },
        }
    },
    watch:{
        video_model(data){
            if(!data){return;}
            //  初始化视频
            const obj = {};
            if(this.video_model == 'live'){         //  获取直播流
                obj.dispatch = 'getLiveUriInfo',
                obj.params = {chnid: this.$route.query.chnid}
            }else if(this.video_model == 'vod'){    //  获取视频流
                obj.dispatch = 'getVodeUriInfo',
                obj.params = { vodid: this.$route.query.vod}
            }
            this.$store.dispatch(obj.dispatch,obj.params).then(({data}) => {
                if(data.success == "true"){
                    this.video.src = data.uri
                }else{
                    //  判断是否在直播流是否在推送
                    if(this.baseInfo.streamActive == false && this.video_model == 'live'){
                        this.$store.commit('alert',{
                            text: '直播还没开始</br>敬请期待'
                        });
                        return false;
                    }else{
                        this.$store.commit('alert',{
                            text: data.msg
                        })
                    }
                }
            })
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
            onlineIDs: state => state.HeartBeanService.onlineIds
        }),
        poster: function(){
            return this.ApiConfig.domain + this.baseInfo.topimg
        },
        video_model: function(){
            return this.baseInfo.playmode
        },
        ...mapGetters(['getOnlineIdWithName']),
    },
    methods:{
        //  触发视频播放，开始在线计费
        startPlay: function(){
            var _this = this;
            this.$store.dispatch('startOnlineJson',{
                objType: this.video_model,
                objId: this.chnid
            }).then(({data})=>{
                if(data.success == "true"){
                    //  提交视频播放的 onlineID
                    _this.$store.commit('pushOnlineId',{
                        name: _this.video_model,
                        value : data.onlineId
                    });
                }else{
                    _this.$store.commit('alert',{
                        text: data.msg
                    })
                }
            })
        },
        //  触发视频结束，结束在线计费
        endPlay(){
            let id = this.getOnlineIdWithName(this.video_model);
            if(id){
                this.$store.dispatch('stopOnlineJson',{
                    onlineId: id
                }).then(({data})=>{
                    if(data.success == "true"){
                        //  删除视频播放的 onlineID
                        this.$store.commit('removeOnlineId',id);
                    }else{
                        this.$store.commit('alert',{
                            text: data.msg
                        })
                    }
                })
            }
        },
        //  浮出控制面板
        show_controls(){
            this.controls_show = true;
            setTimeout(function(){
                this.controls_show = false;
            }.bind(this),5000);
        },
        //  播放/暂停切换
        viedo_switch(){
            if (this.playing){
                this.video_pause()
            }else{
                this.video_play()
            }
        },
        //  播放视频
        video_play(){
            //  判断是否在直播流是否在推送
            if(this.baseInfo.streamActive == false && this.video_model == 'live'){
                this.$store.commit('alert',{
                    text: '直播还没开始</br>敬请期待'
                });
                return false;
            }
            if(this.hasInit){
                this.playing = true;
                this.dom.videoPlayer.play();
            }else{
                //  开始播放
                this.hasInit = true;
                this.playing = true;
                this.dom.videoPlayer.play();
            }
            this.startPlay();   //  开始视频在线
        },
        //  暂停视频
        video_pause(){
            this.playing = false;
            this.dom.videoPlayer.pause();
            this.endPlay();     //  结束视频在线
        },
        //  视频全屏
        video_fullscreen(){
            //  判断是否在直播流是否在推送
            if(this.baseInfo.streamActive == false && this.video_model == 'live'){
                this.$store.commit('alert',{
                    text: '直播还没开始</br>敬请期待'
                });
                return false;
            }
            // android，请求全屏
            if(this.isAndroid){
                if(!this.fullScreen){
                    this.x5VideoOrientation = 'landscape'
                    $('#c-tab').css('opacity',0);
                }else{
                    this.x5VideoOrientation = 'portrait'
                    $('#c-tab').css('opacity',1);
                }
                this.fullScreen = !this.fullScreen;
            }
            //ios，请求全屏
            if(typeof this.dom.videoPlayer.webkitEnterFullscreen === 'function') {
                this.dom.videoPlayer.webkitEnterFullscreen();
            }
        },
        //  视频结束
        vodEnd(){
            this.endPlay();
        }
    },
    mounted: function(){
        this.$nextTick(()=>{
            

            var _this = this;
            _this.dom.videoPlayer = this.$refs.video;   //  视频源

            window.addEventListener('resize',function(){
                _this.dom.videoPlayer.style.width = window.innerWidth + "px";
                _this.dom.videoPlayer.style.height = window.innerHeight + "px";
            });

            //  X5内核全屏
            _this.dom.videoPlayer.addEventListener('x5videoenterfullscreen',function () {
                _this.video_play();
            });
            _this.dom.videoPlayer.addEventListener('x5videoexitfullscreen',function () {
                if(isAndroid){
                    _this.x5VideoOrientation = 'protrait';
                    $('#c-tab').css('opacity',1);
                }
                _this.video_pause();
            });
            // android 监听屏幕全屏事件，进入全屏播放视频，退出全屏时自动暂停播放
            $(document).on('webkitfullscreenchange', function(event) {
                if(document.webkitIsFullScreen) {
                    _this.video_play()
                } else {
                    _this.video_pause()
                }
            });
            // ios 监听屏幕全屏事件，进入全屏播放视频，退出全屏时自动暂停播放
            $(_this.dom.videoPlayer).on('webkitbeginfullscreen', function() {
                _this.video_play()
            }).on('webkitendfullscreen', function() {
                _this.video_pause()
            });
        })
        
    }
}
</script>




