
export default {
    state:{
        //  在线标识IDs
        onlineIds:[

        ],
        //  弹幕列表
        chat:[

        ],
        //  礼物列表
        gifts:[

        ],
        //  公告队列
        notifys: [

        ],
        //  蛇形队列
        zoneNotifys: [

        ],
        //  抽奖箱子
        boxes: [

        ]
    },
    getters:{
        //  获取第一条公告
        firstNotice: (state) => {
            return state.notifys[0] || null;  
        },
        //  获取全部在线IDs
        onlinIDArr: (state) => {
            const arr = [];
            state.onlineIds.map((data)=>{
                arr.push(data.value)
            })
            return arr;
        },
        //  根据名称获取在线ID
        getOnlineIdWithName: (state) => (name) =>{
            var id = null;
            state.onlineIds.forEach((data)=>{
                if(data.name == name){ id = data.value}
            })
            return id;
        },
        //  获取排序的箱子
        boxes: (state) => {
            return state.boxes.sort((a,b)=>{
                return a.enroll_b - b.enroll_b;
            })
        },
        //  获取四条蛇形公告
        zoneNotifys: (state) => {
            return state.zoneNotifys.slice(0,4);
        }
    },
    mutations:{
        //  添加一条在线记录
        pushOnlineId(state,params){
            state.onlineIds.push(params);
        },
        //  删除一条在线记录
        removeOnlineId(state,id){
            state.onlineIds = state.onlineIds.filter((data)=>{
                return data.value != id
            })
        },
        //  添加一条弹幕
        addChat(state,data){
            state.chat.push(data);
            //  清除过多的弹幕节点
            if(state.chat.length > 100){
                state.chat.splice(0,30);
            }
        },
        //  添加一个礼物
        addGifts(state,data){
            state.gifts.push(data);
            //  清除过多的礼物节点
            if(state.gifts.length > 100){
                state.gifts.splice(0,30);
            }
        },
        //  添加多条公告
        addNotify(state,data){
            state.notifys = state.notifys.concat(data);
            state.notifys.sort((a,b) => {   //  排序
                return a.stamp - b.stamp;
            }); 
        },
        //  移除第一条公告
        removeNotify(state){
            state.notifys.shift();  // 弹出队头
        },
        //  添加蛇形公告
        addZoneNotify(state,data){
            state.zoneNotifys.push(data);
            state.zoneNotifys.sort((a,b) => {   //  排序
                return a.stamp - b.stamp;
            }); 
        },
        //  移除蛇形公告
        removeZoneNotify(state){
            state.zoneNotifys.shift(); 
        },
        //  清除超时宝箱
        clearBoxes(state){  //  清除过期宝箱
            let now = Math.floor(new Date().getTime()/1000);
            const arr = [];
            state.boxes.map((box)=>{
                if(box.draw > now){
                    arr.push(box);
                }
            })
            state.boxes = arr;
        },
        //  添加宝箱
        addBoxes(state,data){
            this.commit('clearBoxes');
            data.forEach((item) => {
                if(state.boxes.every((box)=>{
                    return box.id != item.id
                })){
                    state.boxes.push(item);
                }
            })
        },
        //  移除所有数据
        clearService(state){
            state.onlineIds = [];
            state.chat = [];
            state.gifts = [];
            state.notifys = [];
            state.zoneNotifys = [];
            state.boxes = [];
        }
    }
}


