<template>
    <div class="chat-box" id="chat-box">
        <a href="javascript:;" class="box-base box-1" :key="box.id" :ref="'box-id-'+box.id" @click="lotteryShowJson(box.id)" v-for="box in boxes" v-if="box.type == '定时'">
            <div class="box-count-icon"></div>
            <div class="box-text" >{{box.enroll_e | format_time(now,box.enroll_b,box.enroll_e,box.draw)}}</div>
        </a>
        <a href="javascript:;" class="box-base box-2" :key="box.id" :ref="'box-id-'+box.id" @click="lotteryShowJson(box.id)" v-else-if="box.type == '即兴'">
            <div class="box-text" >{{box.enroll_e | format_time(now,box.enroll_b,box.enroll_e,box.draw)}}</div>
        </a>
        <a href="javascript:;" class="box-base box-3" :key="box.id" :ref="'box-id-'+box.id" @click="lotteryShowJson(box.id)" v-else-if="box.type == '竞猜'">
            <div class="box-text" >{{box.enroll_e | format_time(now,box.enroll_b,box.enroll_e,box.draw)}}</div>
        </a>

    </div>
</template>
<script>
import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    data(){
        return {
            now : Math.floor(new Date().getTime()/1000)
        }
    },
    computed:{
        ...mapGetters(['boxes']),
    },
    filters:{
        format_time(data,now,start,end,draw){
            if(start > now){
                return '准备';
            }else if(end > now){
                let diff = data - now; 
                var minute = ('00'+Math.floor(diff/60%60).toString()).slice(-2);
                var second = ('00'+Math.floor(diff%60).toString()).slice(-2);
                return minute + ':' + second;
            }else if(draw > now){
                return '待开奖';
            }else{
                return '已结束';
            }
        }
    },
    methods:{
        lotteryShowJson(id){
            this.$store.dispatch('lotteryShowJson',{
                lotteryid: id
            }).then(({data}) => {
                data.lotteryid = id;

                //  测试内容
                // data.type = 2,
                // data.msg = "测试标题"
                // data.selects = [
                //     {
                //         "text": 'item1',
                //         "value": 'item1-value'
                //     },
                //     {
                //         "text": 'item2',
                //         "value": 'item1-value'
                //     },
                //     {
                //         "text": 'item3',
                //         "value": 'item1-value'
                //     },
                //     {
                //         "text": 'item4',
                //         "value": 'item1-value'
                //     }
                // ]
                this.$emit('lotteryShowJson',data);
            })
        }
    },
    mounted(){
        this.$nextTick(() => {
            setInterval(() => {
                this.now = Math.floor(new Date().getTime()/1000)
            },1000)
        })
    }
}
</script>



