import Vue from 'vue'
import Router from 'vue-router'
import LoginPage from '@/pages/LoginPage'
import IndexPage from '@/pages/IndexPage'
import ShowPage from '@/pages/ShowPage'
import HomePage from '@/pages/HomePage'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage
    },
    {
      path: '/',
      name: 'IndexPage',
      component: IndexPage
    },
    {
      path: '/home',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/show',
      name: 'ShowPage',
      component: ShowPage,
    }
  ]
})



