<template>
    <div class="c-list">
        
        <list-bar-component v-on:loadDataEmit="loadListData"></list-bar-component>

        <list-container-component v-bind:listData="listData"></list-container-component>

        <div class="loading" id="loading" v-show="listLoading">
            <img src="../assets/img/loading.png" alt="">正在加载
        </div>
    </div>
</template>

<script>
import ListBarComponent from '@/components/ListBarComponent.vue'
import ListContainerComponent from '@/components/ListContainerComponent.vue'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: "ListComponent",
    components:{
        ListBarComponent,
        ListContainerComponent,
    },
    data() {
        return {
            swiper: null,
        };
    },
    computed:{
        ...mapState ({
            listData: state => state.ApiService.room.listData,
            listConfig: state => state.ApiService.room.listConfig,
			listLoading: state => state.ApiService.room.listLoading
        })
    },
    methods:{
        loadListData(config = {}){
            this.$store.commit('initList',config);
            this.$store.dispatch('loadListData',config)
        }
    },
    mounted() {
        this.loadListData();
    }
}
</script>



