<template>
  <div id="app" >
    <router-view/>
    
    <loading-component v-if="loading"></loading-component>
    <alert-component v-if="alert.text"></alert-component>
  </div>
</template>

<script>
import {mapMutations, mapState, mapGetters} from 'vuex'
import LoadingComponent from "@/components/LoadingComponent.vue";
import AlertComponent from "@/components/AlertComponent.vue";

export default {
  name: 'App',
  computed: {
    ...mapState ({
      loading: state => state.NotifyService.loading,
      alert: state => state.NotifyService.alert,
    })
  },
  components: {
    LoadingComponent,
    AlertComponent
  },
  mounted(){
    window.onload = function(){
      //  适配尺寸调整
        var baseSize = 16, // 基数
            baseWidth = 375, //量取值大小
            fontSize =  (document.documentElement.clientWidth / baseWidth * baseSize);
        document.getElementsByTagName('html')[0].style.fontSize = fontSize+'px';
        window.addEventListener('resize',function(){
            document.getElementsByTagName('html')[0].style.fontSize = fontSize =  (document.documentElement.clientWidth / baseWidth * baseSize)+'px';
        });
    }
  }
}
</script>



