<template>
    <div class="list-bar">
        <div class="list-bar-item" ref="lisBarItem">
            <a href="javascript:;" v-on:click="list_change($event)" class="active">最热</a>
            <a href="javascript:;" v-on:click="list_change($event)">推荐</a>
            <a href="javascript:;" ref="listSelect">
                <div class="list-select-text">{{title}}</div>
                <select name="" id="list_select" v-on:change="list_change($event)" ref="type_select" >
                    <option value="">全部</option>
                    <option :value="item.id" v-for="item in roomType">{{item.text}}</option>
                </select>
            </a>
        </div>
        <form class="list-search" action=""  v-on:submit.prevent="search_room">
            <input type="search" name="search" v-model="loadData.searchStr" ref="keyword" placeholder="请输入内容">
            <button type="submit"></button>
        </form>
    </div>
</template>

<script>


export default {
    data(){
        return {
            roomType: null,
            title: '分类',
            loadData:{
                sort: '最热',
                categoryid: null,
                searchStr: '',
            }
        }
    },
    methods:{
        list_change(event){
            if(event.type == 'click'){
                for(var item of this.$refs.lisBarItem.children){
                    item.classList = '';
                }
                event.target.classList = 'active';
                this.loadData.sort = event.target.text;
            }else if(event.type == 'change'){
                var currentOption = this.$refs.type_select.options[this.$refs.type_select.options.selectedIndex];
                this.title = currentOption.innerHTML;
                this.loadData.categoryid = currentOption.value;
            }
            this.loadData.searchStr = '';
            this.$store.commit('endList',false);
            this.$emit('loadDataEmit',this.loadData);
        },
        search_room(){
            var keyword = this.$refs.keyword.value.trim();
            if(keyword){
                this.loadData.searchStr = keyword;
                this.$store.commit('endList',false);
                this.$emit('loadDataEmit',this.loadData);
            }
        }
    },
    mounted: function() {
        this.$store.dispatch('getRoomType').then(({data}) => {
            this.roomType = data
        }); 
    },
}
</script>



