<template>
    <div class="tab-rank">
        <div class="rank-nav">
            <div :class="['rank-nav-item',currentIndex == index ? 'active' : '']" v-for="(item,index) in rank" v-on:click="chang_rank(index)">{{item.rank}}</div>
        </div>

        <div class="rank-container">
            <div :class="['rank-list',currentIndex == index ? 'active' : '']" v-for="(item,index) in rank">
                <div class="rank-list-item" v-for="(item,index) in item.list">
                    <div class="bg-tab_rank1" v-if="index==0"></div>
                    <div class="bg-tab_rank2" v-if="index==1"></div>
                    <div class="bg-tab_rank3" v-if="index==2"></div>
                    <div class="rank-other" v-if="[0,1,2].filter(item=>item!==index).length == 3">NO.{{index+1}}</div>
                    <div class="rank-info">
                        <p><span class="chat-vip-user">{{item.name}}</span></p>
                        <p class="rank-build">{{item.value}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapMutations, mapState, mapGetters} from 'vuex'
export default {
    data(){
        return {
            currentIndex: 0,
            rank: null
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
        }),
    },
    methods:{
        chang_rank(index){
            this.currentIndex = index;
        }
    },
    created(){
        this.$nextTick(() => {
            this.$store.dispatch('getRankingJson',{
                chnid: this.chnid
            }).then(({data})=>{
                this.rank = data;
            })
        })
    }
}
</script>




