import Vue from 'vue'
import Vuex from 'vuex'

import BaseInfoService from './BaseInfoService'
import ApiService from './ApiService'
import NotifyService from './NotifyService'
import HeartBeanService from './HeartBeanService'

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    BaseInfoService,
    ApiService,
    NotifyService,
    HeartBeanService
  }
})



