export default {
    state: {
        chnid: null,
        baseInfo: {
            tabs:[]
        },
    },
    getters:{
        //  获取Tab数组
        tabs: (state) => {
            const arr = [];
            var activeIndex = 0;
            state.baseInfo.tabs.forEach((tab,index) => {
                if(tab.val == state.baseInfo.activetab){
                    activeIndex = index;   
                }
                arr.push({
                    index: index,
                    text: tab.text,
                    val: tab.val
                })
            })
            return {
                activeIndex: activeIndex,
                data: arr
            };
        }
    },
    mutations: {
        setChnid(state,chnid){
            state.chnid = chnid;
        },
        setBaseInfo(state,data){
            state.baseInfo = data;
        },
        clearBaseInfo(state){
            state.chnid = null
            state.baseInfo = {
                tabs:[]
            }
        }
    }
}


