<template>
    <ul class="chat-msg">
        <li v-for="item in Mixs" 
            v-if="item.content" >
            <span class="chat-vip" v-if="!!item.viplevel">{{item.viplevel}}</span>
            <span class="chat-level" v-if="!!item.userlever">{{item.userlever}}</span>
            <span class="chat-user">{{item.sender}}</span>
            <span>：{{item.content}}</span>
        </li>
        <li v-else-if="item.gift" >
            <span class="chat-vip" v-if="!!item.viplevel">{{item.viplevel}}</span>
            <span class="chat-level" v-if="!!item.userlever">{{item.userlever}}</span>
            <span class="chat-user">{{item.sender}}</span> 
            <span>送给</span>
            <span class="chat-user">{{item.reciver}}</span> 
            <span class="chat-gift-text"> {{item.gift}} × <b style="font-weight:600">{{item.quantity}}</b></span>
        </li>

    </ul>
</template>
<script>

import {mapState} from 'vuex';

export default {
    props:['chat','gifts'],
    computed:{
        Mixs: function(){
            //  组合 聊天+礼物数组， 根据发送时间（stamp）从小到大排序
            return this.chat.concat(this.gifts).sort((a,b) =>{
                return a.stamp - b.stamp
            })
        }
    },
    watch:{
        Mixs(data){
            $('.chat-msg').animate({'scrollTop':100000})
        }
    }
}
</script>




