<template>
    <div class="alert" v-html="alert.text"></div>
</template>
<script>
import {mapMutations, mapState, mapGetters} from 'vuex'
export default {
    computed:{
        ...mapState({
            alert: state => state.NotifyService.alert,
        })
    },
    mounted:function(){
        this.$nextTick(()=>{
            setTimeout(()=>{
                this.$store.commit('alert',{});
            },this.alert.time)
        })
    }
}
</script>




