<template>
    <div class="zone">
        <ul>
            <li v-for="(item) in zoneNotifys">
                <span>{{item.content}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
import {mapMutations, mapState, mapGetters} from 'vuex'
export default {
    computed:{
        ...mapGetters(['zoneNotifys']),
    },
}
</script>

<style lang="scss">
    $psSize: 750;		//设计稿尺寸
    $baseWidth: 375;	//基础尺寸
    $baseSize: 16;		//基础字体大小
    @function rem($size) {
        @return floor((($baseWidth/$psSize)*$size/$baseSize)*1000000)/1000000 + rem;
    }
    @keyframes zone {
        0%{
            transform: translateX(-100%);
        }
        100%{
            transform: translateX(0);
        }
    }
    .zone{
        width: 100%;
        position: absolute;
        top:rem(50);
        bottom: rem(86);
        z-index: 0;
        ul{
            display: flex;
            flex-direction: column;
            li{
                display: flex;
                font-size: rem(28);
                margin-bottom: rem(30);
                transform: translateX(-100%);
                animation: zone .1s forwards;
                span{
                    display: inline-block;
                    background: #fa6203;
                    color: #fff;
                    padding: rem(20) rem(30);
                    position: relative;
                    &:after{
                        content: "";
                        height: 100%;
                        width: rem(30);
                        position: absolute;
                        top: 0;
                        left: 100%;
                        background: url('data:image/png;base64,') no-repeat;
                        background-size: cover;
                    }
                }
            }
        }
    }
</style>



